seDialog(false)\">\n\t\t\t\t{{ t('files', 'Cancel') }}\n\t\t\t</NcButton>\n\t\t\t<NcButton variant=\"primary\" @click=\"closeDialog(true)\">\n\t\t\t\t{{ t('files', 'Rename') }}\n\t\t\t</NcButton>\n\t\t</template>\n\t</NcDialog>\n</template>\n\n<style module>\n.dialogConfirmFileHidden__checkbox {\n\tmargin-top: 1rem;\n}\n</style>\n"],"sourceRoot":""}]);
